





































import Vue from "vue";

export default Vue.extend({
  data: () => ({
    selection: {},
  }),
  created() {
    this.selection = this.$store.getters.getWheelSelection;
  },
  methods: {
    addColumn() {
      this.selection.colors++;
    },
    removeBlock() {
      this.selection.colors--;
    },
  },
});
