





















































































import Vue from "vue";
import summaryRow from "./summary.vue";
import helpers from "mr-kernel/modules/helpers";

export default Vue.extend({
  components: { summaryRow },
  props: {
    db: {
      type: Array,
    },
  },
  data: () => ({
    helpers,
  }),
  computed: {
    projects() {
      return {
        ...this.$root.projects,
        ...this.$root.groups,
      };
    },
    database() {
      return this.db;
    },
  },
});
