






































































import Vue from "vue";
import wikiColors from "../db/wiki-colors";

export default Vue.extend({
  data: () => ({
    gColorsDB: wikiColors,
    selection: {},
  }),
  created() {
    this.selection = this.$store.getters.getCubeSelection;
  },
  methods: {
    updateSpeed(newVal) {
      this.selection.cubeRotation = parseInt(newVal);
    },
  }
});
