

































import Vue from "vue";

export default Vue.extend({
  props: {
    title: {
      type: String,
      default: "",
    },
    color: {
      type: String,
      default: 'gravel',
    }
  },
  data: () => ({
    blockVisibility: false,
  }),
  created() {
    this.$emit('created');
  },
  methods: {
    removeBlock($event) {
      this.$emit('onRemove', $event);
    },
    toggleBlockVisibility() {
      this.blockVisibility = !this.blockVisibility;
    }
  }
});
