







































































import Vue from "vue";
import toggleRow from "./toggle-row.vue";

export default Vue.extend({
  components: { toggleRow },
  props: {
    project: {
      type: String,
      default: "",
    },
  },
  data: () => ({
    hasSlots: undefined,
  }),
  computed: {
    projects() {
      return {
        ...this.$root.projects,
        ...this.$root.groups,
      };
    },
  },
  methods: {
    sentToProjector(src) {
      this.$store.commit("setProject", {
        value: src,
      });
      console.clear();
    },
  },
  mounted() {
    this.hasSlots = this.$slots.default?.length;
  },
});
